.list-group {
    width: 250px;
}

.list-group-item {
    border-radius: 0px 10px 10px 0px;
    margin: 0px 0px 5px 0px;
}

.drugScreen {
    display: flex;
    flex-direction: row;
}

.drugLabel{
    background: #FFEBCC;
    border-radius: 0 0px 20px 20px;
    padding: 5px;
    height: 55px;
    width: 285px;
    justify-content: center;
    text-align: center;
    align-self: center;
    margin: auto;
}

.drugLocation {
    margin: auto;
}

.drugBar {
    display: flex;
    border: 4px solid black;
    height: 400px;
    width: 20%;
    /* margin: auto; */
    align-self: flex-end;
    border-radius: 20px 0px 0px 20px;
}

.drugInfo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}

.rate{
    margin-left: auto;
    font-family: 'Roboto', sans-serif;
    font-size: 30px;
}

.dosage {
    margin-right: auto;
    font-family: 'Roboto', sans-serif;
    font-size: 30px;
}

.drugCalcs {
    display: flex;
    flex-direction: row;
}